___CSS_LOADER_EXPORT___.push([module.id, `body {
  /* background-color: #505279; */
  /* background-color: #1e212d; */

  background-color: #1e2024;

  /* background-color: #303339; */
  /* background-color: #3d4149; */
  /* background-color: #084d6b; */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
}

main {
  /* background-size: cover; */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  width: 100%;
  height: 95vh;
}

.appName {
  width: 100%;
  /* height: 12vh; */
  /* background-color: #1e212d; */

  /* background-color: #101218; */

  background-color: #0b0c10;

  /* color: #babcea; */
  color: #bacfea;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 20px 0;
}
.appIcon {
  font-size: 3rem;

  text-align: center;
  align-content: center;
}

h1 {
  font-size: 2.5rem;
  margin: 0;
  text-align: center;
  align-content: center;
}
.appContent {
  height: 60vh;
  width: 30%;
  min-width: 400px;
  background-size: cover;

  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
}

form {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
input {
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: rgba(30, 32, 45, 0.6);
  width: 60%;
  padding: 8px;

  color: rgb(209, 209, 209);
  font-weight: 600;
}
input::placeholder {
  color: rgb(209, 209, 209);
}
button {
  font-size: 1rem;
  font-weight: 600;
  color: #1e212d;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  outline: none;
  padding: 8px;
  background-color: rgba(228, 228, 228, 0.8);
}

button:hover {
  background-color: rgba(30, 32, 45, 0.8);
  color: rgb(228, 228, 228);
}

.weatherDisplay {
  width: 80%;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 1.2rem;

  display: flex;
  flex-direction: column;
  gap: 10px;

  padding: 20px 20px;

  /* margin-bottom: 80px; */
}

.locName {
  font-size: 2rem;
  /* font-weight: 600; */
}

.timeAndTemp {
  display: flex;
  justify-content: space-between;
  align-items: last baseline;

  font-size: 1.5rem;
}

.locDescription {
  line-height: 1.5rem;
}

fieldset {
  border: none;
  display: flex;
  gap: 10px;
  align-self: flex-end;
  margin: 50px 10px 20px 0;
}

.tempDiv {
  display: flex;
  align-items: start;

  font-size: 1.3rem;
  font-weight: 600;
  color: #1e212d;
}

input[type="radio"] {
  width: 20px;
  height: 20px;
  accent-color: #101218;
}

input[type="radio"]:hover {
  cursor: pointer;
}

footer {
  color: #bacfea;
  width: 100%;
  height: 5vh;
  background-color: #101218;

  display: flex;
  justify-content: center;
  align-items: center;
  /* font-weight: 600; */

  font-size: 0.9rem;
}
footer a {
  color: #bacfea;
  /* text-decoration: none; */
}

footer a:hover {
  text-decoration: underline;
}

footer p {
  display: flex;
  gap: 10px;
}
#githubSpan {
  display: flex;
  align-items: center;
  gap: 5px;
}

#githubLogo {
  width: 18px;
}

/* //////////////////////////////// */
@media (width<500px) {
  main {
    gap: 30px;
    height: 90vh;
  }

  .appName {
    padding: 10px 0;
  }

  h1 {
    font-size: 1.8rem;
  }
  .appContent {
    width: 50%;
    min-width: 350px;
    min-height: 460px;
    gap: 10px;
  }
  form {
    margin-top: 30px;
    margin-bottom: 20px;
  }

  input {
    width: 60%;
    padding: 8px;
  }

  button {
    padding: 8px;
  }
}
